/*footer style*/
  
footer{
    margin: 50px;
    font-family: 'Abel', sans-serif;
    color: #f1f1f1;
    font-size: 0.8em;
  }
  
  
  .footer{
    display: flex;
    padding: 5px;
  }
  
  .footer-info{
