*{
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}

  body{
    margin: 0px;
    text-align: center;
    color: #ffffff;
  }
  
  /*Nav bar style*/
  .nav-bar{
    box-shadow: none;
    display: inline-block;
    text-align: center;
    background-color: #272421;
    width: 100%;
    height: 60px;
  }
  
  .nav-wrapper{
    height: 100%;
  }
  
  .list-wrapper{
    height: 100%;
  }
  
  .brand-logo{
    margin-right: auto;
    margin-left: 10px; 
    font-size: 22px;
    color: #f6efec;
    font-family: 'Staatliches', sans-serif;
  }
  
  .right{
    margin: 0px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    height: 100%;
  }
  
  li{
    list-style: none;
    font-family: 'Josefin Sans', sans-serif;
  }
  
  li>a{
    text-decoration: none;
    color: #d5c8c3;
  }
  
  li:hover{
    opacity: 0.8;
    cursor: pointer;
  }
  
  .nav-items{
    margin: 10px;
    font-size: 18px;
  }
  
  .sidenav, .sidenav-trigger{
    display: none;
  }
  /*Nav bar style end*/
  
  
  /*carousel style*/
  .carousel-wrapper{
    position: relative;
    overflow: hidden;
    font-family: 'Josefin Sans', sans-serif;
  }
  
  #carousel>.carousel-slide>img{
    width: 5%;
    float: left;
  }
  
  #carousel>.carousel-slide{ 
    position: relative;
    width: 2000%;
    margin: 0;
    left: 0;
    font-size: 0;
  }
  
  .overlay-text{
    position: absolute;
    top: 0;
    text-align: left;
    color: white;
    padding: 20px;
    z-index: 2;
  }
  
  .overlay-link{
    position: absolute;
    background: linear-gradient(rgba(0,0,0,0.4),rgba(0,0,0,0.4));
    width: 100%;
    height: 100%;
    left: 0;
    z-index: 1;
  }
  
  .overlay-text{
    display: flex;
    width: 100%;
  }
  
  .carousel-info{
    margin-right: auto;
  }
  
  .carousel-info>h1>span{
    font-size: 0.5em;
  }
  
  .carousel-info>h1,h2,p{
    margin-bottom: 5px;
  }
  /*carousel style end*/
  
  
  .container{ 
    width: 100%;
  }
  
  .sub-container{
    width: 80%;
    margin: 30px auto;
  }
  
  .sub-heading{
    text-align: left;
    margin: 15px 0px 0px 0px;
    color: #203f49;
    font-family: 'Josefin Sans', sans-serif;
  }
  
  .sub-heading-line{
    margin: 5px 0px;
    height: 3px;
    background-color: #baecf8;
    border: none;
    border-radius: 2px;
  }
  
  .row{
    position: relative;
    margin: 10px 50px;
    background-color: transparent;
  }
  
  .explore-row{
    height: 300px;
    overflow: hidden;
    white-space: nowrap;
    scroll-behavior: smooth;
  }
  
  .img-wrapper{
    display: inline-block;
    margin: 15px 5px;
    height: 80%;
    width: 132px;
    text-align: center;
    vertical-align: top;
    transition: all 0.2s ease-in-out;
  }
  
  .img-wrapper:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    transition: -webkit-transform 0.4s ease-in-out;
    transition: transform 0.4s ease-in-out;
    transition: transform 0.4s ease-in-out, -webkit-transform 0.4s ease-in-out;
  }
  
  .explore-row>.img-wrapper>a>img{
    height: 100%;
    width: 100%;
    border-radius: 5px;
  }
  
  .mov-title{
    display: block;
    white-space: pre-wrap;
    color: #baecf8;
    font-family: 'Abel', sans-serif;
    font-weight: bold;
  }
  
  /*Navigate arrows*/
  .explore{
    padding: 10px;
    color: #a1d5e2;
  }
  
  .fas:hover{
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
    transition: -webkit-transform 0.5s ease-in-out;
    transition: transform 0.5s ease-in-out;
    transition: transform 0.5s ease-in-out, -webkit-transform 0.5s ease-in-out;
  }
  
  .fa-angle-left{
    position: absolute;
    cursor: pointer;
    left: -30px;
    top: 35%;
    font-size: 3em; 
  }
  
  .fa-angle-right{
    position: absolute;
    cursor: pointer;
    right: -30px;
    top: 35%;
    font-size: 3em;
  }

  .home>hr{
    border: 0;
    height: 2px;
    margin: 10px;
    background-image: linear-gradient(to right, rgba(255, 255, 255, 0), rgba(255, 255, 255, 0.75), rgba(0, 0, 0, 0));
  }
  
  @media only screen and (max-width: 768px) {
    /* For mobile phones: */
    .sub-container{
        width: 80%;
        margin: 30px 10%;
    }
  
    .carousel-info{
        font-size: 0.7em;
    }
  }
body{
    margin: 0px;
    text-align: left;
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    font-family: 'Cabin', sans-serif;
}

/*Grid layout*/
.grid-container{
    display: grid;
    grid-gap: 10px;
    grid-template-columns: 100%;
    grid-template-rows: minmax(60px, auto) minmax(100vh, auto) auto;
}

.grid1{
    grid-row: 1;
}

.grid2{
    grid-row: 2;
}

.grid3{
    grid-row: 3;
}


/*Grid layout end*/

.fa-arrow-left{
    padding: 30px 30px;
    font-size: 2.5em;
    color: #ffffff;
    opacity: 0.5;
    transition: -webkit-transform 0.3s ease-in-out;
    transition: transform 0.3s ease-in-out;
    transition: transform 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out;
}

.fa-arrow-left:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    transition: -webkit-transform 0.3s ease-in-out;
    transition: transform 0.3s ease-in-out;
    transition: transform 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out;
}

.info-wrapper{
    text-align: center;
    width: 100%;
    color: #ffffff;
}

.poster-img{
    display: inline-block;
    vertical-align: top;
    width: 25%;
    border-radius: 2px; 
}


.info{
    display: inline-block;
    text-align: center;
    margin: 0px 10px;
    width: 55%;
    height: 100%;
}

p{
    line-height: 1.25em;
}

/*main info heading styles*/
.info>h1{
    margin: 0px 0px 15px 0px;
    font-size: 2em;
    font-family: 'Monda', sans-serif;
}

.info>h2{
    width: 50%;
    margin: 0px auto 10px auto;
    font-size: 1.25em;
}

.info>h3{
    margin: 10px 0px;
}

.info-subheading{
    font-family: 'Syncopate', sans-serif;
    font-size: 1.25em;
    color: rgb(233, 203, 36);
    background-color: #0c3158;
    padding: 5px;
    border-radius: 2px;
}

.inline-wraps{
    display: flex;
    justify-content: center;
}

.inline-wrap-items{
    display: inline;
    padding: 10px;
}

.hr-style{
    border: 0;
    height: 2px;
    background-image: linear-gradient(to right, rgba(228, 228, 192, 0), rgba(228, 228, 192, 0.75), rgba(0, 0, 0, 0));
}

.genre{
    display: inline-block;
    margin: 5px;
    padding: 5px;
    background-color: #202833;
    color: #66fcf1;
    border-radius: 5px;
    font-weight: normal;
}

.tagline>h2{
    width: 50%;
    margin: 5px auto;
}

/*Content wrapper*/
.content-wrapper{
    height: auto;
    background-color: rgb(228, 228, 192);
    color: #000000;
}

.subcontent-wrapper{
    margin: 0px 15%;
    padding: 30px 0px 10px 0px;
}

.subcontent-wrapper>h2{
    padding: 15px 0px 0px 0px;
    margin: 0px;
    font-family: 'Syncopate', sans-serif;
}

.subcontent-wrapper>hr{
    margin: 5px 0px;
    height: 3px;
    background-color: rgb(233, 203, 36);
    border: none;
    border-radius: 2px;
}

.row{
    position: relative;
    margin: 0px;
    background-color: transparent;
}

.cast-list{
    height: 350px;
    overflow: hidden;
    white-space: nowrap;
    scroll-behavior: smooth;
}

.img-wrapper{
    display: inline-block;
    margin: 15px 5px;
    height: 70%;
    width: 132px;
    text-align: center;
    vertical-align: top;
    transition: all 0.2s ease-in-out;
}

.cast-list>.img-wrapper>img{
    height: 100%;
    width: 100%;
    border-radius: 5px;
}

.cast-name{
    display: block;
    white-space: pre-wrap;
    color: rgb(51, 51, 28);
}

/*Navigate arrows*/
.nav-arrows{
    padding: 10px;
    margin: 0px;
    color: rgb(114, 100, 24);
    transition: -webkit-transform 0.2s ease-in-out;
    transition: transform 0.2s ease-in-out;
    transition: transform 0.2s ease-in-out, -webkit-transform 0.2s ease-in-out;
}

.nav-arrows:hover{
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
    transition: -webkit-transform 0.3s ease-in-out;
    transition: transform 0.3s ease-in-out;
    transition: transform 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out;
}

.fa-angle-left{
    position: absolute;
    cursor: pointer;
    left: -25px;
    top: 30%;
    font-size: 3em; 
}

.fa-angle-right{
    position: absolute;
    cursor: pointer;
    right: -25px;
    top: 30%;
    font-size: 3em;
}


@media only screen and (max-width: 768px) {
    /* For mobile phones: */
    .subcontent-wrapper{
        margin: 0px;
        padding: 0px 5%;
    }
}
